/* Products.css */

body {
    font-family: 'Nunito', sans-serif;
    width: 100vw;
    height: 100vh;
    color: #fff;
    background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
    background-size: 400% 400%;
    -webkit-animation: Gradient 15s ease infinite;
    -moz-animation: Gradient 15s ease infinite;
    animation: Gradient 15s ease infinite;
}

.products {
    padding: 20px;
    padding-top: 20px;
    text-align: center;
}

.products .product {
    position: relative;
    overflow: hidden;
    width: 300px;
    height: 520px; /* Increased height to accommodate the button */
    text-align: left;
    display: inline-block;
    background-color: rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 0, 0, 0.1);
    margin: 20px;
    padding: 20px;
    border-radius: 15px; /* Added rounded corners */

}

.products .product img {
    position: relative;
    top: -5px;
    margin: auto;
    transition: all 0.2s ease;
    width: 100%;
    -webkit-clip-path: polygon(0 100%, 0 0, 100% 0, 100% 70%);
    clip-path: polygon(0 100%, 0 0, 100% 0, 100% 70%);
}

.products .product img:hover {
    -webkit-clip-path: polygon(0 100%, 0 0, 100% 0, 100% 100%);
    clip-path: polygon(0 100%, 0 0, 100% 0, 100% 100%);
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
}

.products .product h1 {
    line-height: 20px;
    font-size: 20px;
    margin-top: 15px;
}

.products .product .price {
    text-align: center;
    font-size: 20px;
    margin-top: 25px;
    color: darkblue;
    background: lightblue;
    border: 1px solid blue;
    border-radius: 10px;
    padding: 10px;
}

.products .product .booking-button {
    margin-top: 15px;
    background-color: #1976d2;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.products .product .booking-button:hover {
    background-color: #155a9c;
}

@-webkit-keyframes Gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@-moz-keyframes Gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes Gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
.button-container {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
}

.button-container button {
    background-color: rgba(25, 118, 210, 0.8); /* Semi-transparent blue background */
    color: #fff; /* White text */
    border: none;
    padding: 15px 30px; /* Increased padding for larger buttons */
    font-size: 18px; /* Increased font size */
    border-radius: 25px; /* Rounded corners */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow */
    cursor: pointer;
    transition: background-color 0.3s, transform 0.3s;
}

.button-container button:hover {
    background-color: rgba(21, 90, 156, 0.8); /* Darker semi-transparent blue on hover */
    transform: translateY(-2px); /* Slight lift on hover */
}
.products .product .booking-button {
    display: block;
    margin: 20px auto; /* Center the button */
    background-color: #b36dc7;
    color: rgb(8, 8, 8);
    padding: 10px 30px; /* Increased padding for larger button */
    font-size: 20px; /* Increased font size */
    border: none;
    border-radius: 25px; /* Rounded corners */
    cursor: pointer;
    transition: background-color 0.3s;
}

.products .product .booking-button:hover {
    background-color: #155a9c;
}
